.horizontalScroll {
  padding-top: 10vh;
  white-space: nowrap;
  height: 80vh;
  display: flex;
  align-items: center;
  width: 100vw;
  overflow: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.horizontalScroll::-webkit-scrollbar {
  display: none;
}

